* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: 'Noto Sans Mono', monospace;
    font-weight: 100;
}

body {
    background-image: url("../src/images/bgCoffee.jpg");
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;

}


.todo-app {
    display: flex;
    flex-direction: column;
    justify-content: start;
    width: 520px;
    min-height: 600px;
    background: #01131c;
    text-align: center;
    margin: 128px 800px 128px 150px;
    border-radius: 10px;
    padding-bottom: 32px;
    box-shadow: 8px 8px 24px 0px rgba(66, 52, 26, 1);
}

h1 {
    margin: 32px 0;
    color: #fff;
    font-size: 24px;
}

.complete {
    text-decoration: line-through;
    opacity: 0.4;
}

.todo-form {
    margin-bottom: 32px;
}

.todo-input {
    padding: 14px 32px 14px 16px;
    border-radius: 4px 0 0 4px;
    border: 2px solid #d6a09f;
    outline: none;
    width: 320px;
    background: transparent;
    color: #fff;
}

.todo-input::placeholder {
    color: #e2e2e2;
}

.todo-button {
    padding: 16px;
    border: none;
    border-radius: 0 4px 4px 0;
    cursor: pointer;
    outline: none;
    background: linear-gradient(
            90deg,
            rgba(214,160,159) 0%,
            rgba(107, 77, 87) 100%
    );
    color: #fff;
    text-transform: capitalize;
}

.todo-input.edit {
    border: 2px solid #896A67;
}

.todo-button.edit {
    color: #01131c;
    background: linear-gradient(
            90deg,
            rgba(213,161,160, 1) 0%,
            rgba(250, 216, 214) 100%
    );
    padding: 16px 22px;
}

.todo-container {
    display: flex;
    flex-direction: row;
    position: relative;
}

.todo-row {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 4px auto;
    color: #fff;
    background: linear-gradient(
            90deg,
            rgba(182, 135, 124) 0%,
            rgba(104, 67, 59, 1) 100%
    );

    padding: 16px;
    border-radius: 5px;
    width: 90%;
}

.todo-row:nth-child(4n + 1) {
    background: linear-gradient(
            90deg,
            rgba(137, 106, 103, 1) 0%,
            rgba(107, 77, 87, 1) 100%
    );
}

.todo-row:nth-child(4n + 2) {
    background: linear-gradient(
            90deg,
            rgba(137, 106, 103, 1) 0%,
            rgba(19, 7, 12, 1) 100%
    );
}

.todo-row:nth-child(4n + 3) {
    background: linear-gradient(
            90deg,
            rgba(213,161,160, 1) 0%,
            rgba(137, 106, 103, 1) 100%
    );
}

.icons {
    display: flex;
    align-items: center;
    font-size: 24px;
    cursor: pointer;
}

.delete-icon {
    margin-right: 5px;
    color: #d8d7df;
}

.edit-icon {
    color: #d8d7df;
}
